'use client';

import { useState, useEffect, FormEvent } from 'react';
import { Loader2 } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Switch } from '@/components/ui/switch';
import {
    Dialog,
    DialogContent,
    DialogFooter,
    DialogHeader,
    DialogTitle,
} from '@/components/ui/dialog';
import { PaymentMethod } from '@/types';

interface PaymentMethodFormDialogProps {
    isOpen: boolean;
    onOpenChange: (open: boolean) => void;
    onSubmit: (formData: any, isEdit: boolean) => Promise<void>;
    initialData: PaymentMethod | null;
}

export default function PaymentMethodFormDialog({
    isOpen,
    onOpenChange,
    onSubmit,
    initialData,
}: PaymentMethodFormDialogProps) {
    const [isSaving, setIsSaving] = useState(false);
    const [formData, setFormData] = useState({
        nama_metode: '',
        keterangan: '',
        is_aktif: true,
        icon: null as File | null,
    });

    useEffect(() => {
        if (isOpen && initialData) {
            setFormData({
                nama_metode: initialData.nama_metode,
                keterangan: initialData.keterangan || '',
                is_aktif: Boolean(initialData.is_aktif),
                icon: null
            });
        } else if (isOpen && !initialData) {
            setFormData({
                nama_metode: '',
                keterangan: '',
                is_aktif: true,
                icon: null
            });
        }
    }, [isOpen, initialData]);

    const handleSubmit = async (e: FormEvent) => {
        e.preventDefault();
        setIsSaving(true);
        await onSubmit(formData, !!initialData);
        setIsSaving(false);
    };

    return (
        <Dialog open={isOpen} onOpenChange={onOpenChange}>
            <DialogContent className="sm:max-w-[500px]">
                <DialogHeader>
                    <DialogTitle>{initialData ? 'Edit Metode Pembayaran' : 'Tambah Metode Baru'}</DialogTitle>
                </DialogHeader>
                
                <form onSubmit={handleSubmit} className="space-y-4 mt-2">
                    <div className="space-y-2">
                        <Label>Nama Metode <span className="text-red-500">*</span></Label>
                        <Input 
                            placeholder="Cth: Transfer BCA, QRIS, Cash"
                            value={formData.nama_metode}
                            onChange={(e) => setFormData({...formData, nama_metode: e.target.value})}
                            required
                        />
                    </div>

                    <div className="space-y-2">
                        <Label>Keterangan / No. Rekening</Label>
                        <Textarea 
                            placeholder="Cth: No. Rek 123456 a/n Hatta"
                            value={formData.keterangan}
                            onChange={(e) => setFormData({...formData, keterangan: e.target.value})}
                        />
                    </div>
                    
                    <div className="space-y-2">
                        <Label>Ikon / Logo</Label>
                        <Input 
                            type="file" 
                            accept="image/*"
                            className="cursor-pointer"
                            onChange={(e) => {
                                if (e.target.files?.[0]) setFormData({ ...formData, icon: e.target.files[0] });
                            }}
                        />
                    </div>

                    <div className="flex items-center space-x-2 pt-2">
                        <Switch 
                            id="is-aktif"
                            checked={formData.is_aktif}
                            onCheckedChange={(checked) => setFormData({...formData, is_aktif: checked})}
                        />
                        <Label htmlFor="is-aktif">Status Aktif</Label>
                    </div>

                    <DialogFooter className="pt-4">
                        <Button type="button" variant="outline" onClick={() => onOpenChange(false)}>Batal</Button>
                        <Button type="submit" disabled={isSaving} className="bg-[#D93F21] hover:bg-[#b9351b]">
                            {isSaving && <Loader2 className="mr-2 h-4 w-4 animate-spin" />} Simpan
                        </Button>
                    </DialogFooter>
                </form>
            </DialogContent>
        </Dialog>
    );
}