'use client';

import { useState, useEffect } from 'react';
import axios from '@/lib/axios';
import { toast } from 'sonner';
import { Plus } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { PaymentMethod } from '@/types';

import PaymentMethodFilters from '@/components/admin/payment-method/PaymentMethodFilters';
import PaymentMethodTable from '@/components/admin/payment-method/PaymentMethodTable';
import PaymentMethodFormDialog from '@/components/admin/payment-method/PaymentMethodFormDialog';

export default function PaymentMethodPage() {
    const [methods, setMethods] = useState<PaymentMethod[]>([]);
    const [loading, setLoading] = useState(true);
    const [isDialogOpen, setIsDialogOpen] = useState(false);
    const [editData, setEditData] = useState<PaymentMethod | null>(null);

    const [searchQuery, setSearchQuery] = useState('');
    const [currentPage, setCurrentPage] = useState(1);
    const [itemsPerPage] = useState(10);

    const fetchMethods = async () => {
        setLoading(true);
        try {
            const response = await axios.get('/api/paymentMethods');
            setMethods(response.data);
        } catch (error) {
            toast.error("Gagal memuat data metode pembayaran.");
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => { fetchMethods(); }, []);

    // Filter Logic
    const filteredMethods = methods.filter((item) => 
        item.nama_metode.toLowerCase().includes(searchQuery.toLowerCase())
    );

    // Pagination Logic
    useEffect(() => { setCurrentPage(1); }, [searchQuery]);
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = filteredMethods.slice(indexOfFirstItem, indexOfLastItem);

    // Handlers
    const handleCreate = () => {
        setEditData(null);
        setIsDialogOpen(true);
    };

    const handleEdit = (item: PaymentMethod) => {
        setEditData(item);
        setIsDialogOpen(true);
    };

    const handleDelete = async (id: number) => {
        if(!confirm("Yakin hapus metode ini?")) return;
        try {
            await axios.delete(`/api/paymentMethods/${id}`);
            toast.success("Dihapus!");
            fetchMethods();
        } catch {
            toast.error("Gagal menghapus.");
        }
    };

    const handleSubmit = async (formData: any, isEdit: boolean) => {
        try {
            if (isEdit && editData) {
                await axios.put(`/api/paymentMethods/${editData.id}`, {
                    nama_metode: formData.nama_metode,
                    keterangan: formData.keterangan,
                    is_aktif: formData.is_aktif
                });
                
                if (formData.icon) {
                    const imgData = new FormData();
                    imgData.append('icon', formData.icon);
                    await axios.post(`/api/paymentMethods/${editData.id}/icon`, imgData);
                }
                toast.success("Metode diperbarui!");
            } else {
                const data = new FormData();
                data.append('nama_metode', formData.nama_metode);
                data.append('keterangan', formData.keterangan);
                data.append('is_aktif', formData.is_aktif ? '1' : '0');
                
                if (formData.icon) {
                    data.append('icon', formData.icon);
                }

                await axios.post('/api/paymentMethods', data);
                toast.success("Metode ditambahkan!");
            }
            setIsDialogOpen(false);
            fetchMethods();
        } catch (error: any) {
            console.error(error);
            const message = error.response?.data?.message || "Gagal menyimpan data.";
            toast.error(message);
        }
    };

    return (
        <div className="space-y-6">
            <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
                <div>
                    <h1 className="text-2xl font-bold text-gray-900">Metode Pembayaran</h1>
                    <p className="text-gray-500 text-sm">Atur opsi pembayaran (Cash, Transfer, QRIS).</p>
                </div>
                <Button className="bg-[#D93F21] hover:bg-[#b9351b]" onClick={handleCreate}>
                    <Plus className="w-4 h-4 mr-2" /> Tambah Metode
                </Button>
            </div>

            <PaymentMethodFilters 
                searchQuery={searchQuery}
                setSearchQuery={setSearchQuery}
            />

            <PaymentMethodTable 
                loading={loading}
                methods={currentItems}
                totalData={filteredMethods.length}
                currentPage={currentPage}
                itemsPerPage={itemsPerPage}
                onPageChange={setCurrentPage}
                onEdit={handleEdit}
                onDelete={handleDelete}
            />

            <PaymentMethodFormDialog 
                isOpen={isDialogOpen}
                onOpenChange={setIsDialogOpen}
                onSubmit={handleSubmit}
                initialData={editData}
            />
        </div>
    );
}
