'use client';

import Image from 'next/image';
import { Pencil, Trash2, Loader2, Search, ChevronLeft, ChevronRight } from 'lucide-react';
import { Button } from '@/components/ui/button';
import {
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableHeader,
    TableRow,
} from '@/components/ui/table';
import { PaymentMethod } from '@/types';

interface PaymentMethodTableProps {
    loading: boolean;
    methods: PaymentMethod[];
    totalData: number;
    currentPage: number;
    itemsPerPage: number;
    onPageChange: (page: number) => void;
    onEdit: (item: PaymentMethod) => void;
    onDelete: (id: number) => void;
}

export default function PaymentMethodTable({
    loading,
    methods,
    totalData,
    currentPage,
    itemsPerPage,
    onPageChange,
    onEdit,
    onDelete
}: PaymentMethodTableProps) {
    
    const totalPages = Math.ceil(totalData / itemsPerPage);
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;

    return (
        <div className="rounded-md border bg-white shadow-sm">
            <Table>
                <TableHeader>
                    <TableRow className="bg-gray-50">
                        <TableHead className="w-[50px] text-center">No</TableHead>
                        <TableHead className="w-[80px]">Ikon</TableHead>
                        <TableHead>Nama Metode</TableHead>
                        <TableHead className="hidden md:table-cell">Keterangan</TableHead>
                        <TableHead>Status</TableHead>
                        <TableHead className="text-right">Aksi</TableHead>
                    </TableRow>
                </TableHeader>
                <TableBody>
                    {loading ? (
                        <TableRow>
                            <TableCell colSpan={6} className="h-24 text-center text-gray-500">
                                <Loader2 className="h-5 w-5 animate-spin inline mr-2" /> Memuat...
                            </TableCell>
                        </TableRow>
                    ) : methods.length === 0 ? (
                        <TableRow>
                            <TableCell colSpan={6} className="h-32 text-center text-gray-500">
                                <div className="flex flex-col items-center justify-center">
                                    <Search className="h-8 w-8 text-gray-300 mb-2" />
                                    <p>Tidak ada metode pembayaran.</p>
                                </div>
                            </TableCell>
                        </TableRow>
                    ) : (
                        methods.map((item, index) => (
                            <TableRow key={item.id} className="hover:bg-gray-50">
                                <TableCell className="text-center text-gray-500">
                                    {(currentPage - 1) * itemsPerPage + index + 1}
                                </TableCell>
                                <TableCell>
                                    <div className="relative h-10 w-10 rounded-md overflow-hidden bg-gray-100 border">
                                        {item.icon_url ? (
                                            <Image 
                                                src={item.icon_url} 
                                                alt={item.nama_metode} 
                                                fill 
                                                className="object-cover"
                                                unoptimized
                                                onError={(e) => (e.target as HTMLElement).style.display = 'none'}
                                            />
                                        ) : <div className="w-full h-full flex items-center justify-center text-xs font-bold text-gray-400">$</div>}
                                    </div>
                                </TableCell>
                                <TableCell className="font-medium text-gray-900">{item.nama_metode}</TableCell>
                                <TableCell className="hidden md:table-cell text-gray-500 text-sm">
                                    {item.keterangan || '-'}
                                </TableCell>
                                <TableCell>
                                    {item.is_aktif ? (
                                        <span className="inline-flex items-center px-2 py-0.5 rounded-full text-xs font-medium bg-green-100 text-green-800">
                                            Aktif
                                        </span>
                                    ) : (
                                        <span className="inline-flex items-center px-2 py-0.5 rounded-full text-xs font-medium bg-gray-100 text-gray-800">
                                            Non-aktif
                                        </span>
                                    )}
                                </TableCell>
                                <TableCell className="text-right space-x-2">
                                    <Button variant="ghost" size="icon" onClick={() => onEdit(item)}>
                                        <Pencil className="h-4 w-4 text-blue-600" />
                                    </Button>
                                    <Button variant="ghost" size="icon" onClick={() => onDelete(item.id)}>
                                        <Trash2 className="h-4 w-4 text-red-600" />
                                    </Button>
                                </TableCell>
                            </TableRow>
                        ))
                    )}
                </TableBody>
            </Table>

            {/* Pagination */}
            {!loading && totalData > 0 && (
                <div className="flex items-center justify-between p-4 border-t">
                    <div className="text-sm text-gray-500">
                         Total: {totalData} Metode
                    </div>
                    <div className="flex items-center space-x-2">
                        <Button variant="outline" size="sm" onClick={() => onPageChange(currentPage - 1)} disabled={currentPage === 1}>
                            <ChevronLeft className="h-4 w-4" />
                        </Button>
                        <span className="text-sm font-medium px-2">Hal {currentPage} / {totalPages}</span>
                        <Button variant="outline" size="sm" onClick={() => onPageChange(currentPage + 1)} disabled={currentPage === totalPages}>
                            <ChevronRight className="h-4 w-4" />
                        </Button>
                    </div>
                </div>
            )}
        </div>
    );
}